"use scrict";
import {HOME} from "@/config/router.js";

export default {
	data() {
		return {
			source: "",
			index: 0,
			login: {
				username: "",
				pwd: ""
			},
			register: {
				mobile: "",
				code: "",
				pwd: "",
				repeat: ""
			}
		}
	},
	computed: {
		loginDisabled() {
			let infor = this.login;
			return !infor.username || !infor.pwd || infor.username.length !== 11 || infor.pwd.length < 6;
		},
		sendCodeDisabled() {
			let infor = this.register;
			return !infor.mobile || infor.mobile.length !== 11;
		},
		registerDisabled() {
			let infor = this.register;
			return !infor.mobile || !infor.pwd || !infor.repeat || infor.mobile.length !== 11 && infor.pwd.length < 6 &&
				infor.pwd !== infor.repeat;
		}
	},
	onLoad(options) {
		this.source = options.source || HOME;
	},
	methods: {
		onLogin: async function () {
			await this.$store.dispatch("user/login", this.login);
			this.$toPage(this.source);
		},
		sendCode: async function () {
			let res = await this.$post(this.$api.SEND_VERIFICATION_CODE, {
				mobile: this.register.mobile,
				type: "REG"
			});
			this.$toast({
				title: res.msg,
				icon: "success"
			});
		},
		onRegister: async function () {
			await this.$post(this.$api.REGISTER, this.register);
			this.$toast({
				title: "注册成功",
				icon: "success"
			});
			this.index = 0;
		}
	}
}
